.home-page {
	// text-shadow: 0px 0px 5px $clr1;
	> section.hero {
		width: 95%;
		// backdrop-filter: blur(10px);
		// background-color: rgba($clr4, 0.2);
		// border-radius: 10px;
		// width: $width;
		// max-width: 450px;
		// margin: 20px;
		// padding: 20px;
		// width: 100%;
		// overflow: hidden;
		.hero-img {
			width: 50%;
			svg,
			img {
				width: 100%;
				animation-name: pop;
				animation-duration: 0.5s;
				animation-timing-function: cubic-bezier(0.64, 0.02, 0.12, 1.29);

				@keyframes pop {
					from {
						transform: scale(0.1);
					}
					to {
						transform: scale(1);
					}
				}
			}
			@include for-mobile-layout-width {
				width: 80%;
			}
		}
		.content {
			margin-bottom: 70px;
			width: 50%;
			// padding: 0 0 0 20px;
			h1 {
				margin-bottom: 10px;
				font-size: 2.5em;
				// text-align: start;
				line-height: 1.05;
				animation-duration: 0.5s;
				animation-timing-function: cubic-bezier(0.64, 0.02, 0.12, 1.29);
				// animation-delay: 1s;
				// transform: translateX(calc(100vw + 100%));
				// animation-name: slide3;
				visibility: hidden;
				
				&.animte {
					animation-name: slide3;
					visibility: visible;
				}
				
				@keyframes slide3 {
					from {
						transform: translateX(calc(100vw + 100%));
						// visibility: visible;
					}
					to {
						transform: translateX(0px);
						// visibility: visible;
					}
				}
			}
			section.menu {
				width: 100%;
				animation-duration: 0.5s;
				animation-timing-function: cubic-bezier(0.64, 0.02, 0.12, 1.29);
				// animation-delay: 2s;
				// transform: translateX(calc(100vw + 100%));
				// animation-name: slide4;
				visibility: hidden;
				
				&.animte {
					animation-name: slide4;
					visibility: visible;
				}
				
				@keyframes slide4 {
					from {
						transform: translateX(calc(-100vw - 100%));
						// visibility: visible;
					}
					to {
						transform: translateX(0px);
						// visibility: visible;
					}
				}
				a,
				button {
					white-space: nowrap;
					display: flex;
					justify-content: center;
					align-items: center;
					text-shadow: none;
					margin: 10px 10px 0 0;
					cursor: pointer;
					text-decoration: none;
					padding: 15px 20px;
					background-color: $clr3;
					border-radius: 1000px;
					// border: 2px solid $clr5;
					font-weight: bolder;
					font-size: 100%;
					line-height: 100%;
					// padding: 0 1.5em;
					// min-width: 0;
					font-size: 0.875em;
					// box-shadow: 0px 0px 5px $clr1;
					&:hover {
						background-color: $clr2;
					}
					svg {
						margin-right: 5px;
					}
				}
				// @include for-mobile-layout-width {
				align-items: center;
				justify-content: center;
				// }
			}
			@include for-mobile-layout-width {
				width: 90%;
				// align-items: center;
				h1 {
					text-align: center;
					font-size: 2rem;
				}
			}
		}
		@include for-mobile-layout-width {
			width: 100%;
		}
	}
}
