.about {
	h1 {
		margin: 40px 0 0 0;
	}
	> main {
		padding: 30px;
		width: 100%;
		@include for-mobile-layout-width {
			padding: 0;
		}
		> section {
			min-width: 375px;
			// width: 100%;
			background-color: white;
			color: black;
			padding: 40px;
			margin: 50px;
			border-radius: 20px;
			text-align: start;
			align-items: flex-start;
			@include for-mobile-layout-width {
				padding: 30px 10px;
				margin: 30px 0;
			}
			img {
				width: 30%;
			}
			.content {
				height: 100%;
				padding: 0 0 0 40px;
				@include for-mobile-layout-width {
					padding: 0 0 0 15px;
				}
				h2 {
					color: black;
					margin-bottom: 10px;
					@include for-mobile-layout-width {
						font-size: 25px;
					}
				}
				p {
					max-width: 45ch;
				}
				a {
					color: black;
					margin-top: 10px;
				}
			}
			pre {
				background-color: $clr4;
				color: $clr3;
				padding: 10px 30px;
				border-radius: 10px;
				@include for-mobile-layout-width {
					padding: 0;
					font-size: 8px;
				}
			}
		}
		section:nth-child(even) {
			align-self: flex-end;
			.content {
				padding: 0 40px 0 0;
				@include for-mobile-layout-width {
					padding: 0 15px 0 0;
				}
			}
		}
		section.card-code {
			.content {
				padding: 0;
				pre {
					@include for-mobile-layout-width {
						padding: 5px;
						max-width: 470px;
					}
				}
			}
		}
	}
}
