	box-sizing: border-box;
}

html {
	font-family: NatrixSans, Arial, Helvetica, sans-serif;
	// font-family: montserrat, Arial, Helvetica, sans-serif;
	color: $clr5;
	// line-height: 1.5em;
	text-align: center;
	// background-size: cover;
}

