	background-size: cover;
	margin: 0;
	overflow-x: hidden;
	font-style: normal;
	font-variant-ligatures: normal;
	font-variant-caps: normal;
	font-variant-numeric: normal;
	font-variant-east-asian: normal;
	font-weight: normal;
	font-stretch: normal;
	// font-size: 1.25em;
	line-height: 1.7;
}
body::-webkit-scrollbar {
	width: 10px;
}
body::-webkit-scrollbar-track {
	background: #414141;
	// border-radius: 7px;
}
body::-webkit-scrollbar-thumb {
	background-color: $clr5;
	border-radius: 10px;
}
body:before {
	z-index: -1;
	content: "";
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	// opacity: 0.8;
	// background-image: url("../../assets/img/logo.svg");
	background-repeat: no-repeat;
	background-position: 50% 50%;
}

// img {
// width: 100%;
// }

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: NatrixSans;
	line-height: 1.2em;
	margin: 0;
	color: $clr6;
	font-weight: bolder;
}

h1 {
	line-height: 0.8em;
	font-size: rem(70px);
}

h2 {
	font-size: rem(40px);
}

p {
	margin: 0;
	max-width: 60ch;
}

a {
	text-decoration: none;
}

a,
button {
	color: $clr6;
	background: none;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
	cursor: pointer;
	z-index: 1;
	text-transform: uppercase;
	// white-space: nowrap;
}

.loader-spinner {
	width: 100%;
	height: 100%;
}
